html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.container {
  width: 1000px;
  padding: 50px;
  margin: 100px auto;
}

.main__tabs {
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
}

.main__tabs a {
  padding: 10px 15px;
  text-decoration: none;
  border: 1px solid #61b8ff;
  border-radius: 5px;
  color: #61b8ff;
  transition: all 0.3s ease;
}

.main__tabs a.is-active {
  background-color: #61b8ff;
  color: #fff;
}

.main__tabs a:hover {
  background-color: #2b9fff;
}

.main__tabs a + a {
  margin-left: 15px;
}

.slides-img {
  width: 100%;
  display: block;
  height: auto;
}

.slides-img.only-modal {
  display: none;
}

.slides.js-modal .slides-img {
  width: 50%;
}

.slides.js-modal .slides-img.only-modal {
  display: block;
}

.slides {
  position: relative;
}

.slider {
  position: relative;
}

.tabs-content {
  position: relative;
}

.tab-content {
  width: 100%;
  position: absolute;
  opacity: 0;
}

#slider2.tab-content {
  visibility: hidden;
}

#slider2.is-visible.tab-content {
  visibility: visible;
}

.tab-content.is-visible {
  position: static;
  opacity: 1;
}

.slides {
  margin: 0 10px;
  border: 1px solid #61b8ff;
  border-radius: 5px;
  overflow: hidden;
}

.slides.js-modal {
  display: flex;
  flex-direction: row-reverse;
  border: none;
}

.slides-body {
  padding: 15px;
}

.slides.js-modal .slides-body {
  width: 50%;
}

.slides-body > :not(:last-child) {
  margin-bottom: 15px;
}

.slides-title {
  font-size: 24px;
}

.slides-text.only-modal {
  display: none;
}

.slides.js-modal .slides-text.only-modal {
  display: block;
}

.slides.js-modal .slides-text.only-tab {
  display: none;
}

.slides-meta {
  color: #cccccc;
  font-size: 12px;
}

.slides-more {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: #61b8ff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 24px;
  font-weight: bold;
  color: #fff;
}

.slides-more:hover {
  background: #2b9fff;
}

.slides.js-modal .slides-more {
  display: none;
}

.slick-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: #61b8ff;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0;
  z-index: 1;
}

.slick-arrow:before {
  content: '';
  width: 15px;
  height: 15px;
  border: 10px solid transparent;
}

.slick-prev {
  right: calc(100% + 15px);
}

.slick-prev:before {
  border-right: 10px solid #fff;
  transform: translateX(-25%);
}

.slick-next {
  left: calc(100% + 15px);
}

.slick-next:before {
  border-left: 10px solid #fff;
  transform: translateX(25%);
}

.modal-slider .slick-prev {
  right: 100%;
}

.modal-slider .slick-next {
  left: 100%;
}

.modal-wrap {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-wrap.is-visible {
  display: flex;
}

.modal {
  position: relative;
  width: 800px;
  padding: 15px;
  background: #fff;
  border-radius: 5px;
  transform: translateY(-50%);
  transition: all 0.3s ease;
}

.modal-wrap.modal-wrap.is-visible .modal {
  transform: translateY(0);
}

.modal-close {
  color: #ffffff;
  background: 0;
  font-size: 24px;
  position: absolute;
  right: -30px;
  top: -30px;
  transition: all 0.3s ease;
}

.modal-close:hover {
  color: #61b8ff;
}

.modal-body {
  position: relative;
}

.modal-slides-counter {
  position: absolute;
  right: 10px;
  bottom: 10px;
  display: inline-flex;
  padding: 5px;
  color: #ffffff;
  border-radius: 5px;
  background: #61b8ff;
}
